.services {
    padding:10rem 3rem 0 3rem;
    display: flex;
    height: 45rem;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 13rem;
    padding-top: 3rem;
    
  }
  .progressBar{
    align-self: end;
    position: absolute;
  }
  .progressName{
    font-size: 16px;
  }
  .progress {
    background: rgb(246, 242, 242);
    height: 10px;
    width: 400px;
    border: 1px solid black;
    border-radius: 25px;
    margin: 1px;
  
  }
  
  .pro-fill {
    text-align: center;
    background: rgb(1, 115, 244);
    height: 10px;
    max-width: 400px;
    border-radius: 30px;
    font-size: 10px;
    font-weight: 600;
  }
 
  /* left side */
  .awesome {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 4rem;
  }
  
  .awesome > :nth-child(1) {
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
  }
  .awesome > :nth-child(2) {
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
  }
  .awesome > :nth-child(3) {
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
  }

  .s-button {
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
  }
  /* right sided */
  .cards {
    position: relative;
  }
  .cards > * {
    position: absolute;
  }
 
  /* blur */
  .s-blur2 {
    left: 14rem;
    top: 8rem;
  }
  
  .s-blur1 {
    top: 13rem;
    left: -18rem;
  }
  
  @media screen and (max-width: 480px) {
    .services {
      margin-top: 0;
      flex-direction: column;
      gap: 5rem;
      height: 83rem;
      padding: 0;
    }
    .awesome > span:nth-child(1) {
      font-size: 12vw;
    }
    .awesome > span:nth-child(2) {
     font-size: 12vw;
    }
    .progressBar{
      position: static;
    }
    .progressName{
      font-size: 14px;
      /* color: aliceblue; */
    }
    .progress {
      width: 85vw;
    }
    .pro-fill {
      max-width: 85vw;
    }
    .cards {
      display: flex;
      flex-direction: column;
      gap: 17rem;
    }
    .cards > * {
      position: static;
    }
  }