.t-wrapper{
    padding: 0rem 3rem 0 3rem;;
    height: 35rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top:7rem;
    flex-direction: column;
    /* gap: 2rem; */
    position: relative;
    /* border: 2px solid red; */
}
.t-heading{
    align-self: start;
}
.t-heading>*{
    font-size: 2rem;
    font-weight: bold;
}
.t-heading span:nth-of-type(1){
    color: var(--orange);
}

.t-blur1{
    left: 38rem;
    top: 16rem;
}
.t-blur2{
    top: 9rem;
    left: 0;
}
.testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width: 30rem;
    height: 14rem;
    /* styles same as work.css */
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}
.testimonial>img{
    width: 7rem;
    height: 7rem;
    border-radius: 100%;
}
.testimonial>span{
    margin-top: 1rem;
    /* color: var(--black); */
}


/* slider */
.t-wrapper .swiper {
    width: 100%;
    height: 70%;
    z-index:0;
}

.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}

.t-wrapper .swiper-pagination-bullet-active{
    background: var(--orange);
}

@media screen and (max-width: 480px){
    
    .t-wrapper{
        padding: 0;
        height: 35rem;
    }

    .t-heading>*{
        font-size: 1.2rem;
    }
        
    .testimonial>img{
        width: 90px;
        height: 90px;
    }
    .testimonial>span{
       font-size: 15px;
    }
  }