.contact-form{
    padding: 0 3rem 0 3rem;
    height: 80vh;
    margin-top: 4rem;
  padding-top: 3rem;
}
.contact-name span{
    width: 8.5rem;
    height: 4rem;
    font-size: 2.4rem;
    margin-left: 23rem;
    padding: 1rem;
    
}

.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
    padding-top: 2.5rem;
}

.c-right .form
{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
    
}

.c-right .user{
    width: 20rem;
    max-width: 20rem;
    height: 2rem;
    padding: 0.3em;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;   
}

textarea{
    height: 6rem!important;
}

.c-blur1{
    top: 1rem;
    left: 8rem;
}

@media screen and (max-width: 480px) {
    .contact-form{
        padding: 0;
        flex-direction: column;
        gap: 5rem;
        height: 25rem;
    }
    .contact-name span{
        font-size: 2rem;
        margin-left: 3rem;
        
    }
    .c-right .user{
        width: 14.5rem;
    }

}   