.portfolio {
    padding: 6rem 3.5rem 0rem 3.5rem;
  

  }
  /* heading */
  .portfolio span:nth-of-type(1) {
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
  }
  
  .portfolio span:nth-of-type(2) {
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
  }
  
  /* slider */
  .portfolio .swiper{
    overflow: visible!important;
      z-index: 0;
  }
  
  .portfolio-slider {
    margin-top: 3rem;
    width: 100%;
  }
  
  .portfolio-slider .swiper-slide {
    width: 20rem;
  }
  .portfolio img {
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
    border-radius: 19px;
    height: 11rem;
  }

  @media screen and (max-width: 480px) {
    .portfolio {
      padding: 0rem .5rem;
      padding-top: 5rem;
    }
    
    .portfolio span:nth-of-type(1) {
     
    font-size: 7vw;
  }
  
  .portfolio span:nth-of-type(2) {
    font-size: 8vw;
  }
}