.footer {
    display: flex;
    align-items: center;
    margin: 0rem -3.5rem;
    position: relative;
  }
  .footer .footer-text{
    font-size: 13px;
  }

  .f-content {
    margin-bottom: 1.5rem;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin-top: 10rem;
    gap: 1rem;
    /* color: white; */
  }
  .f-icons {
    display: flex;
    gap: 2.5rem;
    
  }
  .f-icons i{
    font-size: 1.7rem;
    
  } 
  
  @media screen and (max-width: 480px){
    .f-content{
      transform: scale(.5);
    }
    .footer{
      margin-bottom: 0;
    }
    .footer .footer-text{
      margin-bottom: -10rem;
      font-size: 5.5vw;
    }
    
  }