.n-wrapper {
  height: 10vh;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  position: fixed;
  top: 0;
  z-index: 2;
}

.n-left {
  flex: 1;
  align-items: center;
  justify-content: left;
  display: flex;
  gap: .5rem;
}

.n-right {
  right: 10px;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: 400;
}

.n-list {
  flex: 10;
}

.n-list>ul {
  display: flex;
  gap: 2rem;
  margin-right: 10rem;
}

.n-list>ul :hover {
  color: var(--orange);
  cursor: pointer;
}

.n-button{
  flex: 1;
  margin-right: 2rem;
  margin-top: .5rem;
  z-index: 2;
}

@media screen and (max-width: 480px) {
  .n-list {
    display: none;
  }

  .n-left {
    display: none;
  }

  .n-wrapper {
    display: none;
  }

  .n-button {
    margin-right: 1rem;
    right: 1px;
    top: 20px;
    position: fixed;
    z-index: 1;
    background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
    border-radius: 34px;
    border: none;
    color: white;
    font-size: 16px;
    padding: 11px 26px 11px 26px;
  }
}