.sidebar,
.sideToggle span {
  display: none;
}

@media screen and (max-width: 480px) {

  /* Sidebar.css */
  .sidebar {
    display: block;
    position: fixed;
    left: 0;
    top: 0px;
    width: 10rem;
    height: 100vh;
    background-color: rgb(20, 19, 19);
    z-index: 1;
    color: #fff;
    padding: 20px;
    transition: all .4s ease-in-out;
    border-radius: 0 10px 10px 0;
  }

  /* .active {
    transition: all 0.6s ease-in-out;
    transform: translateX(100%);
  } */

  .remove {
    position: fixed;
    left: 0;
    top: 0px;
    width: 10rem;
    height: 100vh;
    background-color: rgb(20, 19, 19);
    z-index: 1;
    color: #fff;
    transition: all .4s ease-in-out;
    transform: translateX(-100%);
  }

  .sidebar p {
    position: absolute;
    left: 200px;
    top: 0px;
    color: var(--orange);
    cursor: pointer;
  }

  .sideToggle span {
    display: block;
    position: fixed;
    top: 20px;
    left: 30px;
    z-index: 1;
    font-size: 40px;
    cursor: pointer;
    color: var(--orange);

  }

  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }

  .sidebar li {
    margin-bottom: 10px;
  }

  .sidebar a {
    text-decoration: none;
    color: #fff;
  }

  .n-name {
    font-size: 1rem;
    font-weight: bold;
    display: flex;
  }
}